	border: 0;
	outline: 0;
	font-size: 100%;
	vertical-align: baseline;
	background: transparent;
}

html {
	line-height: 1.15;
	-webkit-text-size-adjust: 100%;
}

body {
	line-height: 1;
}

a, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
	display: block;
}

nav ul {
	list-style: none;
}

/* Text-level semantics
   ---------------------------------------------------------- */
b, strong {
	font-weight: bolder;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
	content: '';
	content: none;
}

small {
	font-size: 80%;
}

sub, sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
}

sub {
	bottom: -0.25em;
}

sup {
	top: -0.5em;
}

code, kbd, samp, pre {
	font-family: monospace, monospace;
	font-size: 1em;
}

a {
	margin: 0;
	padding: 0;
	font-size: 100%;
	text-decoration: none;
	vertical-align: baseline;
	background: transparent;
}

ins {
	background-color: #ff9;
	color: #000;
	text-decoration: none;
}

mark {
	background-color: #ff9;
	color: #000;
	font-style: italic;
	font-weight: bold;
}

del {
	text-decoration: line-through;
}

abbr[title], dfn[title] {
	border-bottom: 1px dotted;
	cursor: help;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

hr {
	box-sizing: content-box;
	display: block;
	overflow: visible;
	height: 1px;
	border: 0;
	border-top: 2px solid #cccccc;
	margin: 1em 0;
	padding: 0;
}

/* Embedded content
   ---------------------------------------------------------- */
img {
	border-style: none;
}

/* Forms
   ---------------------------------------------------------- */
button, input, optgroup, select, textarea {
	font-family: inherit;
	font-size: 100%;
	line-height: 1.15;
	margin: 0;
}

button {
	background: none;
	margin: 0;
	padding: 0;
	border: none;
}

button, input {
	overflow: visible;
}

button, select {
	text-transform: none;
}

input, select {
	vertical-align: middle;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
	-webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
	border-style: none;
	padding: 0;
