/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
	color: #f1f1f1;
	font-family: "Nunito", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Ariel, sans-serif;
	/* -- #NG -- */
	font-size: 14px;
	/* --------- */
	line-height: 1.5;
}

@media screen and (min-width: 480px) {
	body,
	button,
	input,
	select,
	optgroup,
	textarea {
		font-size: 16px;
	}
}

h1, h2, h3, h4, h5, h6 {
	clear: both;
	font-family: "Signika", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Ariel, sans-serif;
}

h1 {
	font-size: 2.52em;
}

h2 {
	font-size: 2em;
	line-height: 1.2em;
}

h3 {
	font-size: 1.588em;
	margin-bottom: .4em;
	line-height: 1.2em;
}

h4 {
	font-size: 1.26em;
}

h5 {
	font-size: 1em;
}

h6 {
	font-size: 0.8em;
}

p {
	margin-bottom: .8em;
}

dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", Courier, monospace;
	font-size: 18.75px;
	font-size: 1.17188rem;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code, kbd, tt, var {
	font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 18.75px;
	font-size: 1.17188rem;
}

abbr, acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark, ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	/* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
	box-sizing: inherit;
}

body {
	background: #222;
	/* Fallback for when there is no custom background color defined. */
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

img {
	height: auto;
	/* Make sure images are scaled correctly. */
	max-width: 100%;
	/* Adhere to container width. */
}

figure {
	margin: 0;
	/* Extra wide images within figure tags don't overflow the content area. */
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	cursor: pointer;
}

.su-button.circle-btn-default {
	padding: 4px !important;
	border: none !important;
	border-radius: 10px !important;
}

.su-button.circle-btn-default span {
	background-color: #262626 !important;
	display: flex !important;
	margin: 0 !important;
	padding: 14px 20px !important;
	font-size: 100% !important;
	font-weight: 700 !important;
	line-height: 32px !important;
	text-align: left !important;
	text-transform: uppercase !important;
	border: none !important;
	border-radius: 8px !important;
}

.su-button.circle-btn-default span svg {
	width: 1em !important;
	padding: 5px 16px 5px 0 !important;
	font-size: 1.33333333em !important;
	line-height: .75em !important;
	vertical-align: -.225em !important;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 3px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
	color: royalblue;
}

a:visited {
	color: purple;
}

a:hover, a:focus, a:active {
	color: midnightblue;
}

a:focus {
	outline: thin dotted;
}

a:hover, a:active {
	outline: 0;
}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
	margin: 0 0 1.5em;
	overflow: hidden;
}

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	float: left;
	width: 50%;
}

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	float: right;
	text-align: right;
	width: 50%;
}

/* --------------------------------------------------------------
	Header Menu
   --------------------------------------------------------------*/
.main-navigation {
	display: none;
	max-width: 400px;
	width: 100%;
}

.main-navigation.toggled-on {
	background: #262626;
	display: block;
	position: fixed;
	z-index: 800;
	height: calc(100vh - 64px);
	max-width: 100vw;
	width: 100vw;
	margin-top: 64px;
	overflow-y: scroll;
}

.main-navigation div {
	display: flex;
	justify-content: center;
	padding: 30px 0;
}

.main-navigation div #primary-menu {
	min-width: 320px;
}

.main-navigation div ul {
	justify-content: center;
	max-width: 500px;
}

.main-navigation div ul li {
	position: relative;
}

.main-navigation div ul li a {
	display: flex;
	padding: 17px 30px 15px;
	color: #fff;
	line-height: 28px;
}

.main-navigation div ul li a:hover {
	background: #373737;
}

.main-navigation div ul li a i.dashicons {
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin-top: 0;
	margin-right: 20px;
}

.main-navigation div ul li ul {
	display: none;
	z-index: 99999;
	padding: 0;
}

.main-navigation div ul li ul li a {
	background-color: #2c2c2c;
	padding-top: 17px;
}

.main-navigation div ul .menu-item-has-children > a,
.main-navigation div ul .page_item_has_children > a {
	padding-right: 70px;
}

.main-navigation div ul .menu-item-has-children ul li,
.main-navigation div ul .page_item_has_children ul li {
	margin-left: 4px;
}

.main-navigation div ul .menu-item-has-children ul li .dropdown-toggle .dropdown-icon,
.main-navigation div ul .page_item_has_children ul li .dropdown-toggle .dropdown-icon {
	background-color: #262626;
}

.admin-bar .main-navigation.toggled-on {
	height: calc(100vh - 110px);
}

button.dropdown-toggle {
	padding-right: 10px;
	position: absolute;
	top: 0;
	right: 0;
	z-index: 999;
}

button.dropdown-toggle .dropdown-icon {
	background-color: #2c2c2c;
	display: flex;
	justify-content: center;
	width: 32px;
	height: 32px;
	margin: 14px;
	padding: 8px;
	position: relative;
	border-radius: 50%;
	transition: background-color .2s ease-in-out;
}

button.dropdown-toggle .dropdown-icon .dropdown-symbol {
	color: #fff;
}

button.dropdown-toggle.toggled-on .dropdown-icon {
	transition: background-color .2s ease-in-out;
}

button.dropdown-toggle.toggled-on .dropdown-icon .dropdown-symbol {
	color: #373737 !important;
}

button.dropdown-toggle:hover .dropdown-icon {
	background-color: #373737;
}

button.dropdown-toggle:hover .dropdown-icon .dropdown-symbol {
	color: white !important;
}

@media screen and (min-width: 768px) {
	.main-navigation.toggled-on {
		height: 100%;
		max-width: 320px;
		position: absolute;
		border-right: 2px solid rgba(255, 255, 255, 0.1);
		overflow-y: visible;
	}
	.main-navigation.toggled-on .menu-main-menu-container {
		padding-bottom: 30px;
	}
}

@media screen and (min-width: 1024px) {
	.main-navigation {
		display: block !important;
		height: auto !important;
	}
	.main-navigation .menu-main-menu-container {
		padding-top: 20px;
	}
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
	border: 0;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute !important;
	width: 1px;
	word-wrap: normal !important;
	/* Many screen reader and browser combinations announce broken words as they would appear visually. */
}

.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	clip-path: none;
	color: #21759b;
	display: block;
	font-size: 17.5px;
	font-size: 1.09375rem;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
	/* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
	outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
	display: inline;
	float: left;
	margin-right: 1.5em;
}

.alignright {
	display: inline;
	float: right;
	margin-left: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
	content: "";
	display: table;
	table-layout: fixed;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-header:after,
.site-content:after,
.site-footer:after {
	clear: both;
}

